body {
  margin: 0;
}

.main {
  box-sizing: border-box;
  grid-template-columns: repeat(4, 200px);
  grid-auto-rows: 231px;
  place-content: center;
  place-items: center;
  gap: 30px 40px;
  margin-top: 30px;
  display: grid;
}

@media (width >= 500px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 750px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 1000px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

@media (width >= 1500px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.main__person {
  width: 200px;
  height: 231px;
  transition: transform 1s;
  position: relative;
}

.main__photo {
  position: relative;
}

.main__image {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition: transform 1s;
  display: block;
}

.main__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  display: flex;
  position: absolute;
  top: 43%;
  right: 9px;
}

.main__details {
  text-align: center;
  flex-direction: column;
  display: flex;
}

.main__name {
  color: #2f4f4f;
  margin-top: 8px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: bold;
}

.main__extra {
  display: none;
}

.main__person:hover {
  z-index: 10;
  background-color: #eee;
  transform: scale(1.3);
}

.main__person:hover .main__image {
  border-radius: 25%;
}

.main__person:hover .main__details, .main__person:hover .main__extra {
  background-color: #eee;
}

.main__person:hover .main__extra {
  justify-content: center;
  margin: 10px;
  display: flex;
}
